
.current {
    color: lightblue;
}
body{
    background-color: white;
}
.indent{
    margin-left: 50px;
    margin-right: 50px;
}
nav{
    text-align: center;
    background: linear-gradient(gray, #73917b);
    padding: 10px;
    font-size: 1.7em;
    a {
        color: white;
        text-decoration: none;
        padding: 0 1em;
    }
    &:hover{
        color: darkblue;
    }
}
img{
    max-width: 100%;
    height: auto;
}
h1{
    text-align: center;
}
.main {
    font-size: 20px;
    max-width: 100%;
}
.right {
    padding: 0px 10px 10px 10px;
    background: gray;
    margin: 0px

}
.footer {  
    background: linear-gradient(#73917b, gray);
    text-align: center;
    color: white;
    font-size: 25px;
    padding: 20px;
}
.grid-container {
    background-color: rgb(214, 214, 214);
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: 10px;
    padding: 10px;
    padding-top: 0px;
    padding-bottom: 0px;
}
p{
    font-size: 20px;
    margin: 4px;
}
li{
    font-size: 1.5em;
}
.ad{
    background: lightgray;
    text-align: center;
}
.button {
    background-color: red;
    border: none;
    color: white;
    padding: 30px 120px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin: 2px 2px;
    font-size: 30px;
    cursor: pointer;
    font-weight: bold;

}    
.mynd_ad{
    margin: 8px 2px 6px 2px;
    position: relative;       
    animation: mymove 5s;
}
@keyframes mymove {
    from {left: 200px;}
    to {left: 0px;}
    0%  {opacity: 0;}
    25% {opacity: 0.3;}
    50% {opacity: 0.6;}
    75% {opacity: 0.9;}
    100%{opacity: 1;}
}
  #div4 {animation-timing-function: ease-out;}